.advantage-card{
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  &__icon{
    height: 60px;
    img{
      height: 100%;
      width: auto;
    }
  }
  &__title{
    font-size: 24px;
    line-height: 140%;
    margin-top: 20px;
    color: rgba(9, 26, 58, 0.8);
    opacity: 0.9;
  }
}

.about-card{
  &__title{
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #FEFEFE;
    margin-bottom: 30px;
  }
  &__desc{
    color: rgba(9, 26, 58, 0.8);
    opacity: 0.9;
  }
}

.service-item{
  box-shadow: 2px 2px 10px rgba(91, 29, 0, 0.5);
  border-radius: 20px;
  height: 60px;
  @include center;
  padding: 0 15px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  text-transform: uppercase;
}

.delivery-card,
.owl-card{
  display: flex;
  align-items: center;
  &__icon{
    flex-shrink: 0;
    margin-right: 24px;
    img{
      width: 100%;
      display: block;
    }
  }
}

.delivery-card{
  &__icon{
    width: 80px;
  }
}

.owl-card{
  &__icon{
    width: 148px;
  }
}


@media screen and (max-width: 1100px){
  .about-card{
    &__title{
      margin-bottom: 15px;
    }
  }
}


@media screen and (max-width: 576px){
  .advantage-card{
    &__icon{
      height: 40px;
    }
    &__title{
      font-size: 16px;
    }
  }

  .service-item{
    padding: 6px 10px;
    height: auto;
    min-height: 40px;
    font-size: 10px;
    line-height: 12px;
  }

  .delivery-card{
    &__icon{
      width: 40px;
      margin-right: 12px;
    }
  }

  .owl-card{
    &__icon{
      width: 74px;
      margin-right: 12px;
    }
  }
}
