.display1{
  font-size: 64px;
  line-height: 120%;
  font-family: $font2;
}

.display2{
  font-size: 36px;
  line-height: 140%;
  font-family: $font2;
}

.text16{
  font-size: 16px;
  line-height: 140%;
}

.dark-text{
  color: $dark;
}

.accent-text{
  color: $primary;
}


@media screen and (max-width: 1300px){
  .display1{
    font-size: 48px;
  }

  .display2{
    font-size: 32px;
  }
}


@media screen and (max-width: 768px){
  .display1{
    font-size: 20px;
  }

  .display2{
    font-size: 24px;
  }

  .text16{
    font-size: 12px;
  }
}
