@import "../mixins";
.footer{
  padding: 32px 0;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__info{
    display: flex;
    align-items: center;
    align-self: flex-start;
  }
  &__logo{
    display: block;
    width: 140px;
    flex-shrink: 0;
    margin-right: 32px;
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    font-weight: 300;
    font-size: 10px;
    line-height: 140%;
    @include grid100gap(10px);
    max-width: 294px;
    color: #7687A7;
    p{
      line-height: inherit;
    }
  }
  &__socials{
    display: flex;
    align-items: center;
    margin-top: 20px;
    &-item{
      margin-right: 12px;
      height: 24px;
      &:hover{
        path{
          fill:  $dark;
        }
      }
      svg{
        height: 100%;
        width: auto;
      }
    }
  }
  &__nav{
    display: flex;
    margin: 0 15px;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 58px;
  }
  &__link{
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    text-transform: uppercase;
    color: $dark;
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }
}

.footer-services{
  width: 368px;
  flex-shrink: 0;
  &__title{
    padding-bottom: 12px;
    margin-bottom: 18px;
    border-bottom: 1px solid $primary;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;
  }
  &__links{
    display: grid;
    grid-template-columns: 1fr .8fr;
    gap: 10px 15px;
  }
  &__link{
    font-weight: 300;
    font-size: 12px;
    line-height: 140%;
  }
}

.footer-contacts{
  display: flex;
  flex-direction: column;
  align-items: center;
  &__phone{
    font-weight: 500;
    font-size: 28px;
    line-height: 34px;
    color: $dark;
  }
  &__btn{
    padding: 0 11px;
    margin-top: 12px;
    height: 40px;
    font-size: 16px;
    white-space: nowrap;
  }
  &__mail{
    font-size: 19px;
    line-height: 23px;
    color: $dark;
    margin-top: 12px;
  }
}


@media screen and (max-width: 1300px){
  .footer{
    &__logo{
      width: 128px;
    }
    &__nav{
      flex-direction: column-reverse;
    }
    &__links{
      margin: 32px 0 0;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 10px;
    }
  }

  .footer-services{
    width: auto;
    &__links{
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 1100px){
  .footer{
    text-align: center;
    padding: 32px 0;
    &__inner{
      flex-direction: column;
      padding: 0 8px;
      align-items: center;
    }
    &__nav{
      order: 10;
      margin: 32px 0 0;
    }
    &__logo{
      margin: 0;
    }
    &__info{
      flex-direction: column;
      align-self: unset;
      &-right{
        margin-top: 20px;
      }
    }
    &__socials{
      margin-top: 32px;
      justify-content: center;
    }
  }

  .footer-contacts{
    margin-top: 24px;
    &__btn,
    &__mail{
      margin-top: 24px;
    }
  }
}
