@import 'vars';
html {
    width: 100%;
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

body {
    font-family: $font1;
    font-weight: 400;
    color: $textColor;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    width: 100%;
    max-width: 1620px;
    margin: 0 auto;
    padding: 0 30px;

}

a {
    &:hover {
        color: $primary;
    }
}

@media screen and (max-width: 992px) {
   .container{
       padding: 0 12px;
   }
}
