@import "../vars.scss";
.breadcrumbs {
    padding: 20px 0 33px;
    &__item {
        font-size: 16px;
        line-height: 19px;
        position: relative;
        display: inline;
        &:not(:first-child) {
            &:before {
                content: ' | ';
                position: absolute;

            }
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        display: none;
    }
}
