@import "../mixins";
.service{
  color: #FEFEFE;
  background-image: url(../img/service.webp);
  background-size: cover;
  background-position: center;
  padding-bottom: 100px;
  &__text{
    margin-top: 32px;
    font-size: 20px;
    line-height: 140%;
  }
  &__nav{
    display: flex;
    margin-top: 49px;
  }
  &__btn{
    width: 100%;
    max-width: 450px;
  }
}

._service-grid{
  width: 65%;
  max-width: 1021px;
}

.service-detail{
  padding: 52px 0;
  &__grid{
    margin-top: 52px;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 64px / 3));
    gap: 32px;
  }
  &__more{
    display: none;
  }
}

.service-info{
  background: #F7F7F7;
  padding: 52px 0;
  &._transparent{
    background: transparent;
  }
  &__desc{
    margin-top: 32px;
    @include grid100gap(12px);
    p{
      line-height: inherit;
    }
  }
}

.delivery{
  padding: 52px 0;
  &__grid{
    margin-top: 52px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 32px 60px;
  }
}

.owl-advantages{
  background: #F7F7F7;
  &__desc{
    margin-top: 27px;
  }
}

.service-consult{
  margin-top: 52px;
  .service-info__desc{
    margin-top: 16px;
  }
  .consult__form-grid{
    gap: 24px;
    max-width: 800px;
  }
  &__bottom{
    display: flex;
    align-items: center;
    margin-top: 32px;
  }
  &__btn{
    width: 329px;
    max-width: 100%;
  }
  &__call{
    margin-left: 32px;
    font-size: 16px;
    line-height: 19px;
    a{
      display: block;
      font-weight: 700;
      color: $primary;
      white-space: nowrap;
      &:hover{
        text-decoration: underline;
      }
    }
  }
}

.service-nav{
  position: fixed;
  bottom: 249px;
  right: calc((100% - 1560px) / 2);
  background: #FEFEFE;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  width: calc(1560px * .33);
  padding: 20px;
  @include grid100gap(38px);
  &__group{
    &._active{
      .service-nav__show{
        svg{
          transform: rotate(180deg);
        }
      }
    }
  }
  &__show{
    display: flex;
    align-items: center;
    font-size: 20px;
    line-height: 120%;
    font-family: $font2;
    text-transform: uppercase;
    color: $dark;
    transition: all .3s;
    svg{
      margin-left: 12px;
    }
    &:hover{
      cursor: pointer;
      color: $primary;
      path{
        fill: $primary
      }
    }
  }
  &__hidden{
    padding-top: 12px;
    display: none;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    overflow-y: auto;
    max-height: 377px;
    @include scroll;
  }
  &__link{
    font-size: 16px;
    line-height: 140%;
  }
}

.mob-nav{
  display: none;
}


@media screen and (max-width: 1620px){
  .service-nav{
    right: 30px;
    width: calc((100% - 60px) * .33);
  }
}


@media screen and (max-width: 1300px){
  .service{
    &__text{
      font-size: 18px;
    }
  }

  .service-detail{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 16px));
    }
  }

  .delivery{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .service-nav{
    bottom: 350px;
    &__show{
      font-size: 18px;
    }
    &__links{
      max-height: 200px;
    }
  }
}


@media screen and (max-width: 992px){
  .service{
    text-align: center;
    padding: 52px 0;
    &__text{
      font-size: 12px;
      margin-top: 20px;
    }
    &__nav{
      margin-top: 32px;
      justify-content: center;
    }
    &__btn{
      font-size: 16px;
    }
  }

  ._service-grid{
    width: 100%;
    max-width: 100%;
  }

  .service-detail{
    .display2{
      text-align: center;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 4px));
      gap: 8px;
      margin-top: 20px;
      &:not(._open){
        .service-item:nth-child(6)~.service-item{
          display: none;
        }
      }
      &._open{
        &~ .service-detail__more{
          ._view1{
            display: none;
          }
          ._view2{
            display: block;
          }
          svg{
            transform: rotate(180deg);
          }
        }
      }
    }
    &__more{
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 20px auto 0;
      font-size: 12px;
      line-height: 140%;
      ._view2{
        display: none;
      }
      svg{
        margin-left: 5px;
      }
      &:hover{
        color: $primary;
        path{
          fill: $primary;
        }
      }
    }
    ._full{
      grid-column: 1/-1;
    }
  }

  .service-info{
    padding: 32px 0;
    &__desc{
      margin-top: 12px;
    }
  }

  .delivery{
    padding: 32px 0;
    .display2 br{
      display: none;
    }
    &__grid{
      margin-top: 20px;
      grid-template-columns: 100%;
      gap: 12px;
    }
  }

  .owl-advantages{
    &__desc{
      margin-top: 10px;
    }
    .delivery__grid{
      gap: 20px;
    }
  }

  .service-consult{
    margin-top: 32px;
    text-align: center;
    .service-info__desc{
      margin-top: 12px;
      br{
        display: none;
      }
    }
    .consult__form{
      margin-top: 22px;
      &-grid{
        gap: 12px;
        max-width: 100%;
      }
    }
    &__bottom{
      flex-direction: column;
    }
    &__btn{
      width: 450px;
    }
    &__call{
      margin: 12px 0 0;
    }
  }

  .service-nav{
    display: none;
  }

  .mob-nav{
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
    position: fixed;
    padding: 20px 12px;
    background: #FEFEFE;
    z-index: 10;
    left: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    &~.footer{
      padding-bottom: 90px;
    }
    &__btn{
      flex-grow: 1;
      max-width: 300px;
      font-weight: 700;
      font-size: 10px;
      line-height: 12px;
      padding: 0 10px;
      height: 24px;
      border-radius: 20px;
      box-shadow: 2px 2px 10px rgba(91, 29, 0, 0.5);
      margin: 0 3px;
      text-transform: uppercase;
    }
  }
}


@media screen and (max-width: 576px){
  .delivery{
    .display2{
      font-size: 16px;
      line-height: 120%;
    }
  }
}
