@import "../mixins";
.main{
  padding: 20px 0 106px;
  &__top{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: -8px;
    flex-wrap: wrap;
  }
  &__grid{
    margin-top: 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left{
    max-width: 872px;
  }
  .display1{
    color: $dark;
  }
  &__nums{
    margin-top: 46px;
    display: flex;
  }
  &__num{
    &:not(:last-child){
      margin-right: 60px;
    }
    &-title{
      font-weight: 700;
      font-size: 40px;
      line-height: 120%;
      color: $dark;
      margin-bottom: 8px;
    }
  }
  &__company{
    margin-bottom: 40px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    &-img{
      width: 92px;
      flex-shrink: 0;
      margin-left: 20px;
      img{
        width: 100%;
        display: block;
      }
    }
    &-title{
      text-align: right;
      font-size: 18px;
      line-height: 22px;
    }
  }
  &__subtitle{
    font-size: 40px;
    margin-top: 70px;
    text-align: right;
    font-family: $font2;
    text-transform: uppercase;
    color: $primary;
  }
}

.currencies,
.time{
  display: flex;
  align-items: center;
  margin-top: 8px;
  &__item{
    font-size: 16px;
    line-height: 19px;
    &:not(:last-child){
      margin-right: 24px;
    }
    span{
      color: $primary;
    }
  }
}

.time{
  margin-left: 52px;
}

.motto{
  border-top: 2px solid $primary;
  padding-top: 62px;
  margin-top: 47px;
  &__grid{
    max-width: 1151px;
    width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 40% 50%;
    justify-content: space-between;
  }
  &__title{
    font-size: 35px;
    &:not(:last-child){
      margin-bottom: 25px;
    }
  }
  &__subtitle{
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
  }
  &__desc{
    margin-top: 16px;
    line-height: 140%;
    @include grid100gap(12px);
    p{
      line-height: inherit;
    }
  }
}

.info{
  padding: 82px 0 72px;
  background-image: url(../img/bg.webp);
  background-size: cover;
  background-position: center;
  .display2{
    text-align: center;
  }
}

.advantages{
  &__grid{
    margin: 52px auto 0;
    max-width: 1300px;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 32px 40px;
  }
}

.about{
  margin-top: 72px;
  &__grid{
    margin-top: 52px;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 20px 80px;
  }
}

.scheme{
  padding-top: 72px;
  margin-top: 52px;
  border-top: 2px solid #FEFEFE;
  @media screen and (min-width: 769px){
    .advantage-card{
      &__icon{
        height: 80px;
      }
      &__title{
        font-size: 16px;
        line-height: 140%;
      }
    }
  }
  .advantage-card{
    &:not(:last-child){
      position: relative;
      &:before{
        content: '';
        position: absolute;
        width: 40px;
        height: 32px;
        left: 100%;
        top: 50%;
        transform: translateY(-50%);
        background-image: url(../img/arrow.svg);
        @include bgImgSize(37px);
      }
    }
  }
}

.consult{
  padding: 72px 0;
  &__top{
    text-align: center;
  }
  &__desc{
    margin-top: 16px;
    font-size: 20px;
    line-height: 125%;
  }
  &__form{
    margin-top: 38px;
    &-grid{
      display: grid;
      grid-template-columns: repeat(3,1fr);
      gap: 52px;
    }
  }
  &__btn{
    margin: 52px auto 0;
    width: 450px;
    max-width: 100%;
  }
  &__hint{
    margin-top: 16px;
    text-align: center;
  }
}


@media screen and (max-width: 1300px){
  .main{
    &__left{
      max-width: 650px;
    }
    &__subtitle{
      font-size: 32px;
      margin: 0;
    }
  }

  .motto{
    &__grid{
      grid-template-columns: 42% 54%;
    }
  }

  .advantages{
    &__grid{
      margin-top: 32px;
      grid-template-columns: repeat(2,1fr);
    }
  }

  .scheme{
    @media screen and (min-width: 769px){
      .advantage-card:before{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 1100px){
  .main{
    &__grid{
      display: block;
      margin-top: 32px;
      text-align: center;
    }
    &__left{
      max-width: unset;
    }
    &__nums{
      justify-content: center;
    }
    &__num{
      margin: 0 12px !important;
      max-width: 160px;
    }
    &__right{
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 32px;
    }
    &__subtitle{
      order: -1;
      text-align: center;
    }
    &__companies{
      display: flex;
      justify-content: center;
      margin-top: 20px;
    }
    &__company{
      margin: 0 12px;
      display: flex;
      flex-direction: column;
      align-items: center;
      &-img{
        height: 64px;
        width: auto;
        margin: 0 0 12px;
        order: -1;
        img{
          height: 100%;
          width: auto;
        }
      }
      &-title{
        text-align: center;
      }
    }
  }

  .motto{
    &__grid{
      grid-template-columns: 100%;
      gap: 20px;
    }
    &__title{
      font-size: 28px;
    }
  }

  .about{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .consult{
    &__form{
      &-grid{
        gap: 10px 20px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .consult{
    &__form{
      &-grid{
        grid-template-columns: 100%;
      }
    }
  }
}


@media screen and (max-width: 768px){
  .main{
    padding: 10px 0 32px;
    &__top{
      display: grid;
      grid-template-columns: .85fr 1fr;
      gap: 6px;
      margin: 0;
    }
    &__nums{
      margin-top: 20px;
    }
    &__num{
      max-width: 100px;
      &-title{
        font-size: 20px;
      }
    }
    &__right{
      width: calc(100% + 24px);
      margin-left: -12px;
    }
    &__subtitle{
      text-align: center;
      font-size: 20px;
      line-height: 21px;
    }
    &__company{
      &-img{
        height: 32px;
        margin-bottom: 6px;
      }
      &-title{
        font-size: 12px;
        line-height: 150%;
      }
    }
  }

  .currencies,
  .time{
    margin: 0;
    display: grid;
    grid-template-columns: .8fr 1fr;
    gap: 6px;
    &__item{
      margin: 0 !important;
    }
  }

  .motto{
    margin-top: 20px;
    padding-top: 32px;
    &__title{
      font-size: 20px;
      &:not(:last-child){
        margin-bottom: 12px;
      }
    }
    &__subtitle{
      font-size: 16px;
    }
  }

  .info{
    padding: 32px 0;
  }

  .advantages{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .about{
    margin-top: 32px;
    &__grid{
      margin-top: 32px;
    }
  }

  .scheme{
    margin-top: 32px;
    padding-top: 32px;
    .advantages__grid{
      gap: 43px;
    }
    .advantage-card{
      &:not(:last-child){
        &:before{
          top: 100%;
          left: 50%;
          height: 43px;
          transform: translate(-50%,0);
          background-image: url(../img/arrow2.svg);
          background-size: 18px auto;
        }
      }
    }
  }

  .consult{
    padding: 32px 0;
    &__desc{
      margin-top: 12px;
      font-size: 12px;
    }
    &__form{
      margin-top: 20px;
    }
    &__btn{
      margin-top: 20px;
    }
    &__hint{
      margin: 12px auto 0;
      max-width: 280px;
    }
  }
}


@media screen and (max-width: 576px){
  .currencies,
  .time{
    &__item{
      font-size: 10px;
      line-height: 12px;
    }
  }

  .currencies{
    grid-template-columns: 1fr 1.1fr;
  }

  .time{
    grid-template-columns: .87fr 1fr;
  }
}
