.fg {
  position: relative;
  margin: 0;
  input {
    border: none;
    border-bottom: 2px solid $primary;
    padding: 0;
    text-align: center;
    height: 56px;
    width: 100%;
    font-size: 20px;
    line-height: 125%;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: rgba(101, 101, 101, 0.6);
    }
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}


@media screen and (max-width: 768px){
  .fg{
    input{
      height: 30px;
      border-width: 1px;
      font-size: 12px;
      line-height: 15px;
    }
  }
}
