@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    height: 50px;
    border-radius: 20px;
    background-color: $primary;
    color: #fff;
    font-weight: 700;
    font-size: 20px;
    text-transform: uppercase;
    &:hover{
        color: #fff;
        background-color: darken($primary,10%);
    }
}

.m-btn-outline{
    border: 2px solid $primary;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: #fff;
        path{
            fill: #fff;
        }
    }
}
