@import "../mixins";
.header{
  padding: 1.375em 0;
  background-color: $dark;
  position: relative;
  z-index: 30;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    width: 320px;
    display: block;
    flex-shrink: 0;
    margin-right: 114px;
    img{
      width: 100%;
      display: block;
    }
  }
}

.menu{
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav{
  display: flex;
  align-items: center;
  &__item{
    margin-right: 52px;
    position: relative;
  }
  &__show,
  &__link{
    font-size: 16px;
    line-height: 19px;
    color: rgba(#fff,.8);
    text-transform: uppercase;
    display: flex;
    align-items: center;
    cursor: pointer;
    svg{
      margin-left: 5px;
    }
    &:hover{
      color: $primary;
    }
  }
  &__show{
    color: $primary;
    font-weight: 800;
  }
  &__hidden{
    position: absolute;
    top: 99%;
    left: 0;
    padding-top: 10px;
  }
  &__inner{
    padding: 10px 15px;
    background: #FEFEFE;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
    z-index: 10;
  }
  .submenu{
    @include grid100gap(10px);
    width: 300px;
  }
  &__sublink{
    font-size: 14px;
    line-height: 140%;
    display: flex;
    padding: 5px 0;
  }
  @media screen and (min-width: 993px){
    &__item{
      &:hover{
        .nav__hidden{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    &__hidden{
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
    }
  }
}

.header-action{
  display: flex;
  &__buttons,
  &__buttons2{
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    width: 250px;
  }
  &__buttons2{
    width: 30px;
    margin-left: 31px;
    flex-shrink: 0;
  }
  &__btn{
    height: 40px;
    font-size: 14px;
    text-transform: uppercase;
    border-radius: 20px;
    svg{
      width: 18px;
      height: auto;
      margin-right: 8px;
    }
  }
  &__btn2{
    height: 40px;
    display: flex;
    align-items: center;
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  .m-btn-primary{
    box-shadow: 2px 2px 10px rgba(91, 29, 0, 0.5);
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 1600px) {
  .header{
    &__logo{
      width: 280px;
      margin-right: 40px;
    }
  }

  .nav{
    &__item{
      margin-right: 30px;
    }
  }
}


@media screen and (max-width: 1350px) {
  .header{
    &__logo{
      width: 220px;
      margin-right: 30px;
    }
  }

  .nav{
    &__item{
      margin-right: 15px;
    }
    &__show,
    &__link{
      font-size: 14px;
    }
  }

  .header-action{
    &__buttons2{
      margin-left: 15px;
    }
  }
}


@media screen and (max-width: 1150px){
  .header{
    &__logo{
      width: 180px;
    }
  }

  .header-action{
    &__buttons{
      width: 210px;
    }
    &__btn{
      font-size: 12px;
    }
  }
}



@media screen and (max-width: 1100px){
  .header{
    padding: 10px 0;
    &__logo{
      position: relative;
      z-index: 11;
    }
    &._open{
      .burger{
        background-image: url(../img/burger2.svg);
      }
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .burger{
    position: relative;
    z-index: 11;
    width: 24px;
    height: 24px;
    flex-shrink: 0;
    background-image: url(../img/burger.svg);
    @include bgImgSize(100%);
    display: block;
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    padding: 62px 30px 50px;
    flex-direction: column;
    background: $dark;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    overflow-y: auto;
  }

  .nav{
    width: calc(100% + 60px);
    border-top: 1px solid $primary;
    text-align: center;
    flex-direction: column;
    padding: 20px 30px 0;
    &__item{
      margin: 0 0 30px;
    }
    &__show,
    &__link{
      font-weight: 800;
      font-size: 18px;
      line-height: 22px;
      justify-content: center;
      svg{
        display: none;
      }
    }
    &__hidden{
      opacity: 1;
      pointer-events: unset;
      position: static;
    }
    &__inner{
      background-color: transparent;
      padding: 0;
      box-shadow: none;
    }
    &__sublink{
      font-size: 16px;
      justify-content: center;
      text-align: center;
      color: #fff;
      padding: 2px 0;
    }
    .submenu{
      width: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .menu{
    padding-left: 12px;
    padding-right: 12px;
  }

  .nav{
    width: calc(100% + 24px);
  }
}
